import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OperationService } from 'src/app/services/operation.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { StatusAnalyse } from 'src/app/models/status-analyse.enum';
import { MatTableDataSource, MatSnackBar, PageEvent, MatPaginator, MatDialog, MatDialogRef } from '@angular/material';
import { ResultAnalyzeSearch } from 'src/app/models/result-analyze-search.interface';
import { EsteiraService } from '../services/esteira.service';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import * as moment from 'moment';
import { DialogListHistoryComponent } from '../analyze/dialog-list-history/dialog-list-history.component';
import { DialogHistoricokitComponent } from '../shared/dialog-historico-kit/dialog-historico-kit.component';


@Component({
	selector: 'app-search',
	templateUrl: './search.component.html',
	styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public loading: boolean = false;
	public operations: Operacao[] = [];
	public activities: Atividade[] = [];
	public form: FormGroup;
	public formAgenciaConta: FormGroup;
	public statusList: string[][];
	public dataSource = new MatTableDataSource<ResultAnalyzeSearch>();
	private dialogHistoryRef: MatDialogRef<DialogListHistoryComponent, any>;	
	public displayedColumns: string[] = ['esteiraId', 'dataCarga', 'documento', 'agencia', 'conta',  'status', 'statusAqo', 'operador', 'dataAnalise', 'dataAlteracao', 'alteradoPor', 'actions'];
	//public displayedColumns: string[] = ['esteiraId', 'dataCarga', 'documento', 'agencia', 'conta', 'numeroProcesso', 'numeroPasta',  'status', 'statusAqo', 'operador', 'dataAnalise', 'dataAlteracao', 'alteradoPor', 'actions'];
	public ResultAnalyzeSearch: ResultAnalyzeSearch[] = [];
	public today: Date = new Date();
	private dialogHistoricoKitRef: MatDialogRef<DialogHistoricokitComponent, any>;

	/** Paginator */
	public length: number = null;
	public pageSize: number = 5;
	public pageIndex: number = 0;

	constructor (
		private formBuilder: FormBuilder,
		private operationService: OperationService,
		private esteiraService: EsteiraService,
		private matSnackBar: MatSnackBar,
		public dialog: MatDialog,
	) { }

	ngOnInit() {
		
		/** Lista de Operações COM Template Associado */
		this.loading = true;
		this.operationService.getOperations(true).subscribe(
			response => {
				this.operations = [...response.data];
				this.loading = false;
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter a lista de Operações e Atividades. Se o erro persistir, entre em contato com o suporte.");
				this.loading = false;
			}
		);

		
		this.form = this.formBuilder.group({
			typeConsult: ['E', [Validators.required]],
			operacaoId: ['', [Validators.required]],
			atividadeId: ['', [Validators.required]],
			dataInicial: [''],
			dataFinal: [''],
			documento: [''],
			numeroProcesso: [''],
			numeroPasta: [''],
			status: [''],
		});

		this.formAgenciaConta = this.formBuilder.group({
			typeConsult: ['E', [Validators.required]],
			agencia: [''],
			conta: [''],
			documento: [''],
			numeroProcesso: [''],
			numeroPasta: ['']
		});

		

		this.changeType();
	}

	public changeType(): void {
		if (this.form.value.typeConsult === "E") {
			this.statusList = Object.entries(StatusAnalyse).filter(el => el[1] !== "-1");
		} else {
			this.statusList = Object.entries(StatusAnalyse);
		}
	}

	public setOperation(): void {
		const operation: Operacao = this.operations.find(el => el.operacaoId === this.form.value.operacaoId);
		this.activities = [...operation.atividades];
		this.form.get('atividadeId').enable();
	}

	public search(): void {
		if (this.form.valid) {
			this.loading = true;
			this.length = null
			const form = this.form.value;
			const initialDate = form.dataInicial !== "" ? moment(new Date(form.dataInicial)).format('YYYY-MM-DD') : "";
			const finalDate = form.dataFinal !== "" ? moment(new Date(form.dataFinal)).format('YYYY-MM-DD') : "";
	
			this.esteiraService.findAnalyzes(
				form.typeConsult, form.atividadeId, initialDate, finalDate, form.documento, form.numeroProcesso, form.numeroPasta, form.status, this.pageIndex + 1, this.pageSize
			).subscribe(
				response => {
					console.log(response.data);
					this.loading = false;
					this.dataSource = new MatTableDataSource<ResultAnalyzeSearch>(response.data);
					this.length = response.length;
				},
				error => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loading = false;
				}
			);
		} else {
			this.form.markAllAsTouched();
		}
	}

	public searchAgenciaConta(): void {
		const form = this.formAgenciaConta.value;
		// if(form.agencia.length == 0  && form.conta.length == 0  && form.documento.length == 0) {
		// 	SnackbarComponent.showError(this.matSnackBar, null,"Preencha um  Documento ou Conta");
		// } else if(form.agencia.length > 0  && form.conta.length < 5) {
		if(form.numeroPasta.length == 0 && form.numeroProcesso.length == 0) {
				SnackbarComponent.showError(this.matSnackBar, null,"Preencha o Numero do Processo e/ou da Pasta");
		} else {
			this.loading = true;
			this.length = null
			
			this.esteiraService.findHistoricByProcessOrFolder(
				form.typeConsult, form.numeroProcesso, form.numeroPasta, (this.pageIndex + 1).toString(), this.pageSize.toString()
			).subscribe(
				response => {
					this.loading = false;
					this.dataSource = new MatTableDataSource<ResultAnalyzeSearch>(response.data);
					this.length = response.length;
				},
				error => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loading = false;
				}
			);
		} 
	}

	public changePaginator(event: PageEvent): void {
		this.pageIndex = event.pageIndex;
		this.pageSize = event.pageSize;
		this.search();
	}

	public formatDocument(document: string): string {
		
		if (document && document.length === 14) {
			return document.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g, "$1.$2.$3/$4-$5");
		} 
		
		if (document && document.length === 11) {
			return document.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/g, "$1.$2.$3-$4");
		} 
		
		return document;
	}

	public openHistory(_element): void {
		this.dialogHistoryRef = this.dialog.open(DialogListHistoryComponent, {
			width: '1200px',
			disableClose: false,
			data: {
				agencia : _element.header.agencia,
				documento : _element.header.documento,
				contaCorrente : _element.header.contaCorrente
			}
		});
	}

	public openKitHistory(_element: any): void {
		this.dialogHistoricoKitRef = this.dialog.open(DialogHistoricokitComponent, {
			width: '80%',
			height: '80%',
			disableClose: false,
			data: {
				esteiraId : _element.esteiraId,
				agencia : _element.header.agencia,
				documento : _element.header.documento,
				contaCorrente : _element.header.contaCorrente
			}
		});
	}

	
}
