import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EsteiraService } from '../services/esteira.service';
import { Template } from 'src/app/models/template.model';
import { GalleryItem } from 'src/app/models/gallery-item.interface';
import { MatDialog } from '@angular/material';
import { TemplateTypes } from 'src/app/models/template-types.enum';
import { StatusAnalyse } from 'src/app/models/status-analyse.interface';
import { Atividade } from 'src/app/models/alerta-atividade.interface';

@Component({
	selector: 'app-analyze-detail',
	templateUrl: './analyze-detail.component.html',
	styleUrls:  ['./analyze-detail.component.scss']
})
export class AnalyzeDetailComponent implements OnInit, OnDestroy {

	public loading: boolean = false;
	private analyzeId: string;
	public template: Template;
	public galleries: GalleryItem[] = [];
	public type: number = TemplateTypes.VIEW;
	public status: StatusAnalyse;
	public anexos: any[] = [];
	public atividade: Atividade;

	constructor (
		private activatedRoute: ActivatedRoute,
		private esteiraService: EsteiraService,
		public dialog: MatDialog,
	) { }

	ngOnInit() {
		this.analyzeId = this.activatedRoute.snapshot.paramMap.get('id');
		this.atividade = {nome: "", id: Number(this.activatedRoute.snapshot.paramMap.get('atividadeId'))};
		this.getNewItem(this.analyzeId);
	}

	ngOnDestroy() {
		localStorage.removeItem('displayImages');
	}

	private getNewItem(idActivity: string): void {
		this.esteiraService.getAnalyse(idActivity).subscribe(
			response => {
				const analyze: Object = Object.assign({}, response.data.analyze.root);
				const template: Template = JSON.parse(JSON.stringify(response.data.template));

				template.steps.forEach((step) => {
					step.fieldGroups.forEach((fieldGroup) => {
						fieldGroup.fields.forEach((field) => {
							Object.keys(analyze).forEach((key, index) => {
								if (key === field.name) {
									field.value = Object.values(analyze)[index];
								}
							})
						})
					})
				})
				this.template = template;
				this.galleries = response.data.galleries;
				this.status = response.data.status;
				this.anexos =  response.data.anexo;				
			}
		)
	}
}
