import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ResponseList } from 'src/app/models/response-list.interface';

@Injectable({ providedIn: 'root' })
export class AlteracaoFasesService {
	public urlAlteracaoFases = "/api/v1/alteracao-fases";
    constructor (
        private http: HttpClient,
    ) {}

	public get(tipoId: string, statusId: string, dataInicial: string, dataFinal: string,  pageIndex: string, pageSize: string): Observable<any> {
		return this.http.get<ResponseList>(`${environment.urlServices}${this.urlAlteracaoFases}`, {
			params : {
				tipo : tipoId,
				statusItem : statusId,
				dataInicial : dataInicial, 
				dataFinal : dataFinal,
				pageIndex: pageIndex,
				pageSize: pageSize
			}
		});
    }

	public getTipos(): Observable<any> {
		return this.http.get<ResponseList>(`${environment.urlServices}${this.urlAlteracaoFases}/tipos`);
	}
	
	public getStatus(): Observable<any> {
		return this.http.get<ResponseList>(`${environment.urlServices}${this.urlAlteracaoFases}/status`);
	}
	
	public putAlterarFase(alteracaoFaseItemId: number): Observable<any> {
		return this.http.put<ResponseList>(`${environment.urlServices}${this.urlAlteracaoFases}/item/`+alteracaoFaseItemId,null);
	}

}