import { Component, OnInit } from '@angular/core';
import { EsteiraService } from '../services/esteira.service';
import { Template } from 'src/app/models/template.model';
import { Dialog, ActivityOpenTicket } from 'src/app/models/dialog.model';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { TemplateTypes } from 'src/app/models/template-types.enum';
import { MatDialog, MatDialogRef, MatSnackBar } from '@angular/material';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { OpenTicket } from 'src/app/models/post-analyse.class';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { DialogChoiceActivityComponent } from '../shared/dialog-choice-activity/dialog-choice-activity.component';
import { Atividade } from 'src/app/models/alerta-atividade.interface';

@Component({
  selector: 'app-open-ticket',
  templateUrl: './open-ticket.component.html',
  styleUrls: ['./open-ticket.component.scss']
})
export class OpenTicketComponent implements OnInit {
	
	public template: Template;
	public loading: boolean;
	private activityId: number;
	private timestampInicial: string;
	public type: number = TemplateTypes.OPEN_TICKET;
	public listActivity: ActivityOpenTicket[] = [];
	private dialogRef: MatDialogRef<DialogComponent, any>;
	private dialogRefChoiceAcitivy: MatDialogRef<DialogChoiceActivityComponent, any>;
	public atividade: Atividade;

	constructor (
		public dialog: MatDialog,
		private esteiraService: EsteiraService,
		private matSnackBar: MatSnackBar
	) { }
	
	ngOnInit() {
		this.esteiraService.getStaticOperations().subscribe(
			response => {
				this.listActivity = [...response];
				this.choiceActivity();
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
			}			
		);
	}

	private choiceActivity(): void {
		const dialog: Dialog = new Dialog();
		dialog.title = "Abertura de Chamado";
		dialog.message = "Escolha a atividade";
		dialog.activities = [...this.listActivity];

		this.dialogRefChoiceAcitivy = this.dialog.open(DialogChoiceActivityComponent, {
			width: '600px',
			data: dialog
		});

		this.dialogRefChoiceAcitivy.afterClosed().subscribe(
			response => {
				if (response && response.activity) {
					this.getNewItem(response.activity);
				}
			}
		);
	}

	private getNewItem(idActivity: number): void {
		this.loading = true;
		this.esteiraService.getTemplateToStaticOperation(idActivity).subscribe(
			response => {
				this.loading = false;
				if (response) {
					this.template = response.template;
					this.timestampInicial = new Date().getTime().toString();
					this.activityId = idActivity;
					this.atividade = response.atividade;
				} 
			}, 
			error => {
				this.loading = false;
				SnackbarComponent.showError(this.matSnackBar, null, error.error.message);
			}
		)
	}

	public submit(body: OpenTicket) {

		body.atividadeId = this.activityId;
		body.templateVersaoId = this.template.templateVersaoId;
		body.timestampInicial = this.timestampInicial;

		this.loading = true;
		this.esteiraService.postOpenTicket(body).subscribe(
			() => {
				this.loading = false;
				const dialog: Dialog = new Dialog();
				dialog.title = "Sucesso!";
				dialog.message = "Abertura de Chamado concluída com sucesso.";
				dialog.type = DialogTypes.INFORMATION;
				this.showMessage(dialog);

				this.dialogRef.afterClosed().subscribe(
					() => {
						this.template = null;
						this.choiceActivity();
					}
				);
			},
			(error) => {
				this.loading = false;
				const dialog: Dialog = new Dialog();
				dialog.title = "Análise não concluída";
				dialog.message = error.error.message;
				dialog.type = DialogTypes.INFORMATION;
				this.showMessage(dialog);
			}
		);
	}
	
	public showMessage(dialog: Dialog): void {
		this.dialogRef = this.dialog.open(DialogComponent, {
			width: '600px',
			data: dialog
		});
    }
}
