import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Dialog } from 'src/app/models/dialog.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Usuario } from 'src/app/models/usuario.interface';

@Component({
	selector: 'app-dialog-contingency',
	templateUrl: './dialog-contingency.component.html',
	styleUrls: ['./dialog-contingency.component.scss']
})
export class DialogContingencyComponent implements OnInit {

	public formContingency: FormGroup;
	public arrayUsers: Usuario[];
	public contingencyValidationErrors: boolean;
	public statusIdManagerialView: string;
	
	constructor (
		public dialogRef: MatDialogRef<DialogContingencyComponent>,
		@Inject(MAT_DIALOG_DATA) public data: Dialog,
		private formBuilder: FormBuilder,
	) { }
	
	ngOnInit() {
		this.statusIdManagerialView = this.data['statusId'];
		this.arrayUsers = this.data.users;
		this.formContingency = this.formBuilder.group({
			acao : ['', [Validators.required]],
			operadorId : [''],
			justificativa : [''],
		})
	}

	public dealContingency(): void {
		if (this.formContingency.valid) {
			if (
				(this.formContingency.value.acao === "Finalizar" && this.formContingency.value.justificativa !== "") ||
				(this.formContingency.value.acao === "TransferirUsuario" && this.formContingency.value.operadorId !== "") ||
				(this.formContingency.value.acao === "TriagemManual" && this.formContingency.value.justificativa !== "") ||
				this.formContingency.value.acao === "TransferirFila"
			) {
				this.dialogRef.close(this.formContingency.value)
			} else {
				this.formContingency.markAllAsTouched();
				this.contingencyValidationErrors = true;
			}
		}
	}

}
