import { NgModule } 				from '@angular/core';
import { Routes, RouterModule } 	from '@angular/router';
import { AnalyzeComponent } 		from './analyze/analyze.component';
import { DisplayImagesComponent } 	from './display-images/display-images.component';
import { OpenTicketComponent } 		from './open-ticket/open-ticket.component';
import { SearchComponent } 			from './search/search.component';
import { AnalyzeDetailComponent } from './analyse-detail/analyze-detail.component';
import { ManagerialViewComponent } from './managerial-view/managerial-view.component';
import { ManagerialViewDetailComponent } from './managerial-view-detail/managerial-view-detail.component';
import { ReportBuilderComponent } from './report-builder/report-builder.component';
import { OperationRoutesGuard } from './operation-routes.guard';
import { ReportProductivityComponent } from './report-productivity/report-productivity.component';
import { ReportGeneralAnalyticalComponent } from './report-general-analytical/report-general-analytical.component';
import { FasesTfcComponent } from './fases-tfc/fases-tfc.component';

const routes: Routes = [
	{ path: 'analise', component: AnalyzeComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'abrir-chamado', component: OpenTicketComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'exibir-imagens', component: DisplayImagesComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'pesquisa', component: SearchComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'detalhes-analise/:id/:atividadeId', component: AnalyzeDetailComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'visao-gerencial', component: ManagerialViewComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'visao-gerencial/:back', component: ManagerialViewComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'visao-gerencial/detalhes/:type/:operationId/:activityId/:statusId', component: ManagerialViewDetailComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'gerenciador-relatorio', component: ReportBuilderComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'relatorio-produtividade', component: ReportProductivityComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'relatorio-geral-analitico', component: ReportGeneralAnalyticalComponent, canActivate: [ OperationRoutesGuard ] },
	{ path: 'fases-tfc', component: FasesTfcComponent, canActivate: [ OperationRoutesGuard ] }
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class OperationRoutingModule {
	
}
