import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Dialog, ActivityOpenTicket } from 'src/app/models/dialog.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
	selector: 'app-dialog-choice-activity',
	templateUrl: './dialog-choice-activity.component.html',
	styleUrls: ['./dialog-choice-activity.component.scss']
})
export class DialogChoiceActivityComponent implements OnInit {

	public formChoiceActivity: FormGroup;
	public arrayActivities: ActivityOpenTicket[] = [];
	
	constructor (
		public dialogRef: MatDialogRef<DialogChoiceActivityComponent>,
		@Inject(MAT_DIALOG_DATA) public data: Dialog,
		private formBuilder: FormBuilder,
	) { }
	
	ngOnInit() {
		this.arrayActivities = [...this.data.activities];
		this.formChoiceActivity = this.formBuilder.group({
			activity : ['', [Validators.required]]
		})
	}

	public start(): void {
		if (this.formChoiceActivity.valid) {
			this.dialogRef.close(this.formChoiceActivity.value)
		}
	}

}
