import { Component, OnInit, Inject} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ImageEditor } from 'src/app/models/image-editor';

@Component({
	selector: 'app-dialog-image-edit',
	templateUrl: './dialog-image-edit.component.html',
	styleUrls: ['./dialog-image-edit.component.scss']
})
export class DialogImageEditComponent implements OnInit {

	public state: string = 'default';
	public config: any;
	public url: string;
	public urlCropped: any;
	public iscompare: boolean;
	public imageEditor: ImageEditor;
	public imageEditorCampare: ImageEditor

	constructor(
		public dialogRef: MatDialogRef<DialogImageEditComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any
	) { }

	ngOnInit() {
		const imageEditor = {
			images: this.data.images,
			indiceSelected: this.data.indiceSelected,
			compare: this.data.compare
		}
		this.imageEditor = imageEditor;

		let indiceCompare: number = 0;
		if(this.imageEditor.images.length == 1){
			indiceCompare = this.data.indiceSelected;
		} else if(this.data.indiceSelected < (this.imageEditor.images.length-1)){
			indiceCompare = this.data.indiceSelected+1
		} else {
			indiceCompare = this.data.indiceSelected-1
		}
		const imageEditorCampare = {
			images: this.data.images,
			indiceSelected: indiceCompare,
			compare: this.data.compare
		}
		this.imageEditorCampare = imageEditorCampare;

	}

	public close() {
		this.dialogRef.close();
	}
}
