import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OperationService } from 'src/app/services/operation.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { MatTableDataSource, MatSnackBar, PageEvent, MatPaginator, MatDialog, MatSelectChange } from '@angular/material';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import * as moment from 'moment';
import { ReportBuilderService } from '../services/report-builder.service';
import { ResultAnalyzeReportBuilder, PersonalFields } from 'src/app/models/result-analyze-report-builder.interface';
import { StatusAnalyse } from 'src/app/models/status-analyse.enum';
import { trigger, state, transition, style, animate } from '@angular/animations';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
	selector: 'app-report-builder',
	templateUrl: './report-builder.component.html',
	styleUrls: ['./report-builder.component.scss'],
	animations: [
		trigger('detailExpand', [
			state('collapsed', style({height: '0px', minHeight: '0'})),
			state('expanded', style({height: '*'})),
			transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
		]),
	],
})

export class ReportBuilderComponent implements OnInit {
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	
	public loading: boolean = false;
	public operations: Operacao[] = [];
	public activities: Atividade[] = [];
	public form: FormGroup;
	public dataSource = new MatTableDataSource<ResultAnalyzeReportBuilder>();
	public displayedColumnsDefault: string[] = ['documento', 'agencia', 'contaCorrente', 'dataAnaliseInicial', 'dataAnaliseFinal', 'operador', 'dataCadastro', 'status', 'motivosRejeicao'];
	public displayedColumns: string[] = [];
	public statusList: string[][];

	public length: number = null;
	public pageSize = 5;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public dataAtual: Date = new Date();
	public personalFields: string[] = [];
	public listPersonalFields: PersonalFields[] = [];
	public labelColumns: string[] = [];
	
	public expandedElement: ResultAnalyzeReportBuilder;

	constructor(
		private formBuilder: FormBuilder,
		private operationService: OperationService,
		private serviceCenterService: ReportBuilderService,
		private matSnackBar: MatSnackBar,
		public dialog: MatDialog,
		private sanitizer: DomSanitizer
	) { }
		
	ngOnInit() {
		this.form = this.formBuilder.group({
			operacaoId: ['', Validators.required],
			atividadeId: ['', Validators.required],
			statusId: [''],
			dataInicial: [''],
			dataFinal: [''],
			documento: [''],
			agencia: [''],
			conta: ['']
		});
		this.operacoes();
		this.statusList = Object.entries(StatusAnalyse).filter(el => el[1] !== "-1");
	}
		
	private operacoes() {
		/** Lista de Operações COM Template Associado */
		this.loading = true;
		this.operationService.getOperations(true).subscribe(
			response => {
				this.operations = [...response.data];
				this.loading = false;
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter a lista de Operações e Atividades. Se o erro persistir, entre em contato com o suporte.");
				this.loading = false;
			}
		);
	}
		
	public setOperation(): void {
		const operation: Operacao = this.operations.find(el => el.operacaoId === this.form.value.operacaoId);
		this.activities = [...operation.atividades];
		this.form.get('atividadeId').enable();
		this.pageIndex = 0;
	}
	
	public setActivity(): void {
		this.getPersonalFields();
	}
	

	public getPersonalFields() {
		this.loading = true;
		this.personalFields = [];
		const form = this.form.value;
		this.serviceCenterService.getPersonalFields(form.atividadeId).subscribe(
			response => {

				this.listPersonalFields = response.data;
				this.loading = false;
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter dados campos personalizados. Se o erro persistir, entre em contato com o suporte.");
				this.loading = false;
			}
		);

	}


	public getResult(exportaXLSX: boolean = false) {
		if(this.form.valid) {
			this.loading = true;
			const form = this.form.value;
			const dataInicial = form.dataInicial !== "" ? moment(new Date(form.dataInicial)).format('YYYY-MM-DD') : "";
			const dataFinal = form.dataFinal !== "" ? moment(new Date(form.dataFinal)).format('YYYY-MM-DD') : "";
			const pageIndex = (this.pageIndex  + 1).toString();
			const pageSize = this.pageSize.toString();
			this.serviceCenterService.generate(form.atividadeId, 
												exportaXLSX,
												form.statusId,
												form.documento, 
												form.agencia, 
												form.conta , 
												dataInicial, 
												dataFinal, 
												pageSize, 
												pageIndex,
												this.personalFields ).subscribe(
				response => {
					if (response.type == "application/octet-stream"){
						var dateToday = moment().format('YYYYMMDDHHmmss');
						var fileName = "report_" + this.form.value.atividadeId + "_" + dateToday + ".xlsx";
						var linkArquivo = window.URL.createObjectURL(response);
						var link = document.createElement("a");
						link.setAttribute("href", linkArquivo);
						link.setAttribute("download", fileName);
						link.style.display = "none";
						document.body.appendChild(link);
						link.click();
						document.body.removeChild(link);

						this.length = null;
					}else{
						this.displayedColumns = this.displayedColumnsDefault.map(x=>x);
						for(var i = 1; i <= response.camposPersonalizados.length; i++){
							this.displayedColumns.push("generico"+i);
						}
	
						this.labelColumns = response.camposPersonalizados;
						this.length = response.length;
						this.dataSource = new MatTableDataSource<ResultAnalyzeReportBuilder>(response.data);
						this.dataSource.paginator = this.paginator;
					}


					this.loading = false;
				},
				() => {
					SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter dados. Se o erro persistir, entre em contato com o suporte.");
					this.loading = false;
				}
			);
		}
	}
			
	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getResult();
	}

	public formatDocument(document: string): string {
		//return document;
		if (document == undefined){
			return document
		}
		if (document.length === 14) {
			return document.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g, "$1.$2.$3/$4-$5");
		} else if (document.length === 11) {
			return document.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/g, "$1.$2.$3-$4");
		}
	}


	public setPersonalFields(event: MatSelectChange): void {
		if (event.value.length > 30){
			event.source.value = this.personalFields;
			SnackbarComponent.showAlert(this.matSnackBar, "Somente é possível selecionar 30 colunas personalizadas por consulta.");
			return;
		}
		this.personalFields = [];
		this.personalFields = [...event.value];
	}	
}