import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatTableDataSource, MatSnackBar, PageEvent, MatPaginator, MatDialog, MatDialogRef } from '@angular/material';
import { FasesTfc } from 'src/app/models/fases-tfc.interface';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import * as moment from 'moment';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { AlteracaoFasesService } from '../services/alteracao-fases.service';
import { forkJoin } from 'rxjs';
import { DialogTypes } from 'src/app/models/dialog-types.enum';

@Component({
	selector: 'app-fases-tfc',
	templateUrl: './fases-tfc.component.html',
	styleUrls: ['./fases-tfc.component.scss']
})
export class FasesTfcComponent implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public loading: boolean = false;
	public tipos: any[] = [];
	public status: any[] = [];
	public form: FormGroup;
	public statusList: string[][];
	public dataSource = new MatTableDataSource<FasesTfc>();
	private dialogReference: MatDialogRef<DialogComponent, any>;
	public displayedColumns: string[] = ['etiqueta', 'agencia', 'contaCorrente', 'documento', 'dataCadastro', 'tipoAlteracaoFase', 'status', 'motivoRPA', 'actions'];
	public today: Date = new Date();

	/** Paginator */
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public length: number = null;
	public pageSize: number = 5;
	public pageIndex: number = 0;

	constructor(
		private formBuilder: FormBuilder,
		private alteracaoFasesService: AlteracaoFasesService,
		private matSnackBar: MatSnackBar,
		public dialog: MatDialog,
	) { }

	ngOnInit() {
		this.form = this.formBuilder.group({
			tipoId: [''],
			statusId: [''],
			dataInicial: [''],
			dataFinal: [''],
		});

		this.getTipoStatus();
	}

	public getAlteracaoFases(paginacao: boolean) {
		if(this.form.valid) {
			this.loading = true;
			const formValues = this.form.value;
			const dataInicial = formValues.dataInicial !== "" && formValues.dataInicial !== null ? moment(new Date(formValues.dataInicial)).format('YYYY-MM-DD') : "";
			const dataFinal = formValues.dataFinal !== "" && formValues.dataFinal !== null ? moment(new Date(formValues.dataFinal)).format('YYYY-MM-DD') : "";
			if(!paginacao){
				this.pageIndex = 0;
			}
			const pageIndex = (this.pageIndex  + 1).toString();
			const pageSize = this.pageSize.toString();
			const tipoId = (formValues.tipoId == undefined? '': formValues.tipoId);
			const statusId = (formValues.statusId == undefined? '': formValues.statusId);
			this.alteracaoFasesService.get(tipoId, statusId, dataInicial, dataFinal, pageIndex, pageSize).subscribe(
				response => {
					this.dataSource.data = response.data;
					this.dataSource.paginator = this.paginator;
					this.length = response.length;
					this.loading = false;
				},
				error => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loading = false;
				}
			);
		}
	}


	private getTipoStatus() {
		forkJoin(
			this.alteracaoFasesService.getTipos(),
			this.alteracaoFasesService.getStatus()
		).subscribe(
			([_tipos, _status]) => {
				this.tipos = _tipos.data;
				this.status = _status.data;
				//set default
				this.form.get("statusId").setValue(6);
				this.getAlteracaoFases(false);
			} , error => {
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	public setPageSizeOptions(event: PageEvent): void {
		this.pageIndex = event.pageIndex;
		this.pageSize = event.pageSize;
		this.getAlteracaoFases(true);
	}


	public openModal(registro: string, id: number): void {
		this.dialogReference = this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title: "Confirmar Tratamento Manual",
				message: "Confirmo que acessei o sistema TFC e fiz o tratamento manual do seguinte registro: "+ registro,
				type: DialogTypes.CONFIRMATION
			}
		});

		this.dialogReference.afterClosed().subscribe(
			result => {
				if (result == undefined) {
					SnackbarComponent.showAlert(this.matSnackBar, "Ação cancelada pelo usuário!");
				} else if (result === 'ok') {
					this.alteracaoFasesService.putAlterarFase(id).subscribe(
						() => {
							this.getAlteracaoFases(false);
						},
						error => {							
							SnackbarComponent.showError(this.matSnackBar, error);
						}
					);
				}
			}
		);
	}

}
