import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { MatPaginator, MatTableDataSource, MatDialog } from '@angular/material';
import { ActivatedRoute } from '@angular/router';
import { ManagerialViewService } from '../services/managerial-view.service';
import { ManagerialViewDetail } from 'src/app/models/managerial-view-detail.interface';
import { tap } from 'rxjs/operators';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { GeneralInformationsService } from 'src/app/services/general-informations.service';
import { EsteiraService } from '../services/esteira.service';
import { Router } from '@angular/router';
import {PageConfig } from 'src/app/models/page-config';
import { noop } from 'rxjs';
import { DialogContingencyComponent } from '../shared/dialog-contingency/dialog-contingency.component';

@Component({
	selector: 'app-managerial-view-detail',
	templateUrl: './managerial-view-detail.component.html',
	styleUrls: ['./managerial-view-detail.component.scss']
})
export class ManagerialViewDetailComponent implements OnInit, AfterViewInit {

	@ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
	public loading: boolean = true;
	public dataSource = new MatTableDataSource<ManagerialViewDetail>();
	public displayedColumns = [
		'esteiraId', 'serviceCenterId', 'proposta', 'processo', 'atividade', 'documento', 
		'operador', 'dataCadastro', 'dataAnaliseInicial', 'dataAnaliseFinal', 'action'
	];
	public totalItens: number;
	private type: string;
	private operationId: string;
	private activityId: string;
	public statusId: string;
	public showErrorMessage: boolean = false;
	public operationName: string;
	public activityName: string;
	public statusName: string;
	public mensagem: string;
	public pageConfig: PageConfig;
	public config: string;

	constructor(
		private activatedRoute: ActivatedRoute,
		private managerialViewService: ManagerialViewService,
		public dialog: MatDialog,
		private generalInformationsService: GeneralInformationsService,
		private esteiraService: EsteiraService,
		private router: Router
	) { }
	
	ngOnInit() {
		this.type = this.activatedRoute.snapshot.paramMap.get('type');
		this.operationId = this.activatedRoute.snapshot.paramMap.get('operationId');
		this.activityId = this.activatedRoute.snapshot.paramMap.get('activityId');
		this.statusId = this.activatedRoute.snapshot.paramMap.get('statusId');
		this.statusName = this.getStatusName(this.statusId);
	}

	ngAfterViewInit() {
		this.getData();
		if (this.paginator) {
			this.paginator.page.pipe(
				tap(() => this.getData())
			).subscribe();
		}
	}

	public getData(): void {
		this.loading = true;
		const pageIndex: number = this.paginator ? (this.paginator.pageIndex + 1) : 1;
		const pageSize: number = this.paginator ? this.paginator.pageSize : 5;

		if (this.type === "operacao") {
			this.managerialViewService.getDetailManagerialViewOperation(this.operationId, this.statusId, pageSize, pageIndex).subscribe(
				response => {
					if (response.data.length > 0) {
						this.operationName = response.data[0].operacao;
						this.activityName = "Todas";
						this.totalItens = response.length;
						this.dataSource = new MatTableDataSource<ManagerialViewDetail>(response.data);
						this.showErrorMessage = false;
						this.loading = false;
					} else {
						this.showErrorMessage = true;
						this.loading = false;
					}
				}
			)
		} else if (this.type === "atividade") {
			this.managerialViewService.getDetailManagerialViewActivity(this.operationId, this.activityId, this.statusId, pageSize, pageIndex).subscribe(
				response => {
					if (response.data.length > 0) {
						this.operationName = response.data[0].operacao;
						this.activityName = response.data[0].atividade;
						this.totalItens = response.length;
						this.dataSource = new MatTableDataSource<ManagerialViewDetail>(response.data);
						this.showErrorMessage = false;
						this.loading = false;
					} else {
						this.showErrorMessage = true;
						this.loading = false;
					}
				}
			)
		}
		
	}

	private getStatusName(status: string): string {
		switch (status) {
			case "0":
				return "Atendidas Hoje";
			
			case "1":
				return "Aguardando Análise";
			
			case "2":
				return "Em Análise";
			
			case "3":
				return "Postergado";
			
			case "8":
				return "Em Contingência";

			case "10":
				return "Carga Pendente";				
		
			default:
				break;
		}
	}

	public formatDocument(document: string): string {
		if (document && document.length === 14) {
			return document.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g, "$1.$2.$3/$4-$5");
		} else if (document && document.length === 11) {
			return document.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/g, "$1.$2.$3-$4");
		} else {
			return document;
		}
	}

	public toDeal(element: ManagerialViewDetail): void {

		this.generalInformationsService.getUsersByLine(element.filaId).subscribe(
			response => {
				const dialogRef = this.dialog.open(DialogContingencyComponent, {
					width: '600px',
					data: {
						title : `Tratar ${this.getStatusName(this.statusId)}`,
						message : "Escolha abaixo o que deseja fazer com este processo",
						type : DialogTypes.CONTINGENCY,
						statusId: this.statusId,
						users : response
					}
				});

				dialogRef.afterClosed().subscribe(
					response => {
						if (response !== undefined) {
							if (response.acao === "Finalizar") {
								delete response.operadorId;
							}
							this.esteiraService.finalizeContingencie(element.esteiraId, response).subscribe(
								() => {
									this.getData();
								},
								error => {
									this.dialog.open(DialogComponent, {
										width: '600px',
										data: {
											title : "Atenção",
											message : error.error.message,
											type : DialogTypes.INFORMATION
										}
									});
								}
							)
						}
					}
				)
			}
		)
	}
	
	public setAtividadeEsteira(element: ManagerialViewDetail): void {
		this.loading = true;
		this.esteiraService.putUpdateActivity(element.esteiraId, 90010100).subscribe(
			() => noop,
			error => {
				this.dialog.open(DialogComponent, {
					data: {
						title : "Atenção",
						message : error.error.message,
						type : DialogTypes.INFORMATION
					},
					width: '600px'
				});
			}
		)
		this.router.navigate(['/operacao/visao-gerencial/'])
		this.loading = false;
	}

}
