import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { ResponseList } from '../../../models/response-list.interface';
import { Observable } from 'rxjs';

@Injectable({
	providedIn: 'root'
})
export class ReportBuilderService {
	public urlReportBuilder = "/api/v1/gerenciador-relatorios";
	public urlTemplate = "/api/v1/templates";

	constructor (
		private http: HttpClient,
    ) { }
	
	public generate(atividadeId: string, 
					exportaXLSX: boolean,
					statusId: string, 
					documento: string, 
					agencia: string, 
					conta: string, 
					dataInicial: string, 
					dataFinal: string, 
					PageSize: string, 
					PageIndex: string,
					personalFields: string[] ) {

		const data = {
				atividadeId: atividadeId,
				exportaXLSX: exportaXLSX,
				statusId: statusId,
				documento: documento,
				agencia: agencia,
				contaCorrente: conta,
				dataInicial : dataInicial,
				dataFinal: dataFinal,
				pageSize: PageSize,
				pageIndex: PageIndex,
				camposPersonalizados: personalFields
		};
		
		var options = {}
		if (exportaXLSX){
			options = {responseType : 'blob' as 'json'}
		}

		return this.http.post<any>(environment.urlServices + this.urlReportBuilder, data, options);
	}

	public getPersonalFields(atividadeId: string) {
		return this.http.get<ResponseList>(environment.urlServices + this.urlTemplate + '/atividade/' + atividadeId + '/campos');
	}

	
}