import { Component, OnInit, Input, Output, EventEmitter, AfterViewInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder, AbstractControl } from '@angular/forms';
import { MatDialog, MatSnackBar, MatDialogRef } from '@angular/material';
import { OperationService } from 'src/app/services/operation.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { GalleryItem } from 'src/app/models/gallery-item.interface';
import { PostManualSorting } from 'src/app/models/post-manual-sorting.model';
import { PersonTypes } from 'src/app/models/person-types.enum';
import { DialogHistoricComponent } from './dialog-historic/dialog-historic.component';
import { EsteiraService } from 'src/app/modules/operation/services/esteira.service';
import { BrValidator } from 'src/app/shared/validators/br-validators';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { Dialog } from 'src/app/models/dialog.model';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { ImageEditor } from 'src/app/models/image-editor';
import { AlertaAtividade } from 'src/app/models/alerta-atividade.interface';
import { element } from 'protractor';
import { FormValidator } from 'src/app/shared/validators/form-validators';


@Component({
	selector: 'app-manual-sorting',
	templateUrl: './manual-sorting.component.html',
	styleUrls: ['./manual-sorting.component.scss']
})
export class ManualSortingComponent implements OnInit, AfterViewInit {
	public loading: boolean;
	public form: FormGroup;
	public heightJumbotron: number;
	public operations: Operacao[] = [];
	public operationsAll: Operacao[] = [];
	public activities: Atividade[] = [];
	public validaDocumento: boolean;
	public validaConta: boolean;
	public dialogReference: MatDialogRef<DialogComponent, any>;
	
	public imageEditor: ImageEditor;
	public imageIndex: number = 0;

	@Input() esteiraId: string;
	
	@Input() agencia: string;
	@Input() contaCorrente: string;
	@Input() documento: string;
	@Input() alertaAtividades: AlertaAtividade[];
	@Input() alertaDocumentos: AlertaAtividade[];

	@Input() galleries: GalleryItem[] = [];
	@Output() submit: EventEmitter<any> = new EventEmitter();

	constructor (
		private formBuilder: FormBuilder,
		public dialog: MatDialog,
		private operationService: OperationService,
		private esteiraService: EsteiraService,
		private matSnackBar: MatSnackBar,
	) { }

	ngOnInit() {
		/** Lista de Operações sem Template Associado */
		this.operationService.getOperations(true).subscribe(
			response => {
				this.operationsAll = [...response.data];
				this.loadOperation('cpf');
			},
			() => {
				// Mensagem de Erro para Serviço Indisponível. Não tem como prosseguir;
			}
		);


		let typeDocument = 'cpf';
		if (this.documento != null 
			&& !isNaN(Number(this.documento)) 
			&& Number(this.documento).toString().length > 11){
				typeDocument = 'cnpj';
			}

		this.form = this.formBuilder.group({
			documentType : [typeDocument, Validators.required],
			cpf : [this.documento],
			cnpj : [this.documento],
			bankBranch : [this.agencia],
			bankAccount : [this.contaCorrente],
			operationId : ['', Validators.required],
			activity : ['', Validators.required],
		})

		this.form.get('activity').disable();

		this.imageEditor = {
			images: this.galleries,
			indiceSelected: this.imageIndex,
		}

		if (this.alertaAtividades != undefined && this.alertaAtividades.length > 0){
			this.openAlerta(this.alertaAtividades[0], 'Atividade');
		}
		
		if(this.alertaDocumentos != undefined && this.alertaDocumentos.length > 0){
			this.form.get("cpf").valueChanges.subscribe(
				response => {
					if(this.form.get("cpf").valid) {
						const alerta = this.alertaDocumentos.find(element => element.documento.replace(/[^\d]+/g,'') == response);
						if(alerta != undefined) {
							this.openAlerta(alerta, 'Documento')
						}
					}
				}
			);
			this.form.get("cnpj").valueChanges.subscribe(
				response => {
					const alerta = this.alertaDocumentos.find(element => element.documento.replace(/[^\d]+/g,'') == response);				
					if(alerta != undefined) {
						this.openAlerta(alerta, 'Documento')
					}
				}
			);
		}
	}

	private loadOperation(type: string) {
		const optionType: PersonTypes = (type === "cpf" ? PersonTypes.FISICA : PersonTypes.JURIDICA);
		this.operations = this.operationsAll.filter(el => el.atividades.some(at => at.tipoPessoa === PersonTypes.AMBOS || at.tipoPessoa === optionType));
	}

	ngAfterViewInit() {
		setTimeout(() => {
			const heightHeader: number = document.getElementById('wrap-form').getBoundingClientRect().top + 95;
			const heightForm: number = document.getElementById('wrap-form').getBoundingClientRect().height;
			const heightPage: number = window.innerHeight;
			this.heightJumbotron = heightForm + heightHeader > heightPage ? heightForm : heightPage - heightHeader;
		})
	}

	public salvar(): void {
		
		if(this.form.value.documentType === 'cpf') {
			this.validaDocumento = this.form.get("cpf").valid;
		} else if(this.form.value.documentType === 'cnpj') {
			this.validaDocumento = this.form.get("cnpj").valid;
		} 

		if ( this.validaDocumento && this.form.get("documentType").valid && this.form.get("operationId").valid && this.form.get("activity").valid){
			const digito = this.form.value.bankAccount.slice(-1);
			const conta = this.form.value.bankAccount.toString().substring(0,(this.form.value.bankAccount.toString().length - 1))
			if(BrValidator.conta(this.form.value.bankBranch.toString(),conta,digito)) {
				this.submitForm(true);
			} else {
				let dialog: Dialog = new Dialog();
				dialog.title = "Conta Inválida";
				dialog.message = "A Conta Corrente informada parece está incorreta, deseja prosseguir mesmo assim?";
				dialog.type = DialogTypes.CONFIRMATION;
				this.showMessage(dialog);
				this.dialogReference.afterClosed().subscribe(
					response => {
						if (response === 'ok') {
							this.submitForm(false);
						} 
					}
				);
			}
		} else {
			this.form.markAllAsTouched();
		}
	}

	public showMessage(dialog: Dialog): void {
		this.dialogReference = this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title: dialog.title,
				message: dialog.message,
				type: dialog.type,
			}
		});
	}

	private submitForm(validDigit: boolean) {
		const body: PostManualSorting = new PostManualSorting();
		body.documento = this.form.value.documentType === 'cpf' ? this.form.value.cpf : this.form.value.cnpj;
		body.agencia = this.form.value.bankBranch;
		body.contaCorrente = this.form.value.bankAccount;
		body.atividadeId = this.form.value.activity.id;
		body.isContingencia = false;
		body.doMatch = true;
		body.validDigit = validDigit;
		this.submit.emit(body);	
	}

	public setOperation(): void {
		this.form.value.activityId = null;
		this.form.controls['activity'].setValue(null);
		const optionType: PersonTypes = (this.form.value.documentType === "cpf" ? PersonTypes.FISICA : PersonTypes.JURIDICA);
		const operation: Operacao = this.operations.find(el => el.operacaoId === this.form.value.operationId);
		this.activities = operation.atividades.filter(atv => !atv.atividadeFilha && (atv.tipoPessoa === PersonTypes.AMBOS || atv.tipoPessoa === optionType));
		this.form.get('activity').enable();
		if(this.form.value.documentType == 'cpf') {
			this.form.get('cpf').setValidators(this.cpfValidator);
		} else if(this.form.value.documentType == 'cnpj') {
			this.form.get('cnpj').setValidators(this.cnpjValidator);
		}
		
	}

	public setDocumentType(type: string): void {
		if (this.form.value.documentType !== type) {
			this.activities = [];
			this.form.value.activityId = null;
			this.form.value.operationId = null;
			this.loadOperation(type);
		}
	}

	public seeHistoric(): void {
		this.loading = true;
		this.esteiraService.getHistoric(this.esteiraId).subscribe(
			response => {
				this.loading = false;
				this.dialog.open(DialogHistoricComponent, {
					width: '80%',
					data: response
				});
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loading = false;
			}
		)
	}

	public imageSelected(imageIndex: any) {
		this.imageEditor.indiceSelected = imageIndex;
	}

	public validaCPF(documento: any){
		if(documento.length == 14){
			this.validaDocumento = BrValidator.cpf(documento);
		} else {
			this.validaDocumento = false;
		}
		
	}

	public validaCNPJ(documento: any){
		if(documento.length == 18){
			this.validaDocumento = BrValidator.cnpj(documento);
		} else {
			this.validaDocumento = false;
		}
	}

	public openAlerta(alerta: AlertaAtividade, tipo: string) {
		const dialog: Dialog = new Dialog();
		dialog.title = "Alerta de " + tipo;
		dialog.message = alerta.mensagem;
		dialog.type = DialogTypes.INFORMATION;
		this.showMessage(dialog);
	}

	private cpfValidator(abstractControl: AbstractControl): any {
		const valor = abstractControl.value;
		const validacaoDocumento = abstractControl.parent.value.activity.validacaoDocumento;
		if (BrValidator.validadorCpfAtividade(valor, validacaoDocumento)) {
			return null;
		}
		return { isError: true };
	}

	private cnpjValidator(abstractControl: AbstractControl): any {
		const valor = abstractControl.value;
		const validacaoDocumento = abstractControl.parent.value.activity.validacaoDocumento;
		if (BrValidator.validadorCnpjAtividade(valor, validacaoDocumento)) {
			return null;
		}
		return { isError: true };
	}

	public setAtividade(){
		this.form.get(this.form.value.documentType).updateValueAndValidity();
	}
}
