import { Component, OnInit, OnDestroy } from '@angular/core';
import { EsteiraService } from '../services/esteira.service';
import { Template } from 'src/app/models/template.model';
import { Router } from '@angular/router';
import { MatDialog, MatCheckboxChange, MatDialogRef } from '@angular/material';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { Analyse } from 'src/app/models/post-analyse.class';
import { FormGroup, FormBuilder } from '@angular/forms';
import { PauseTypes } from 'src/app/models/pause-types.interface';
import { GeneralInformationsService } from 'src/app/services/general-informations.service';
import { UtilsService } from 'src/app/services/utils.service';
import { PostManualSorting } from 'src/app/models/post-manual-sorting.model';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DialogManualSortingComponent } from './dialog-manual-sorting/dialog-manual-sorting.component';
import { Dialog } from 'src/app/models/dialog.model';
import { TemplateTypes } from 'src/app/models/template-types.enum';
import { GalleryItem } from 'src/app/models/gallery-item.interface';
import { AlertaAtividade, Atividade } from 'src/app/models/alerta-atividade.interface';

@Component({
	selector: 'app-analyse',
	templateUrl: './analyze.component.html',
	styleUrls: ['./analyze.component.scss'],
})
export class AnalyzeComponent implements OnInit, OnDestroy {
	public template: Template;
	public content: Object;
	public loading: boolean;
	public esteiraID: string;

	public agencia: string;
	public contaCorrente: string;
	public documento: string;

	private interval: any;
	public formPauses: FormGroup;
	public reasonsToPause: PauseTypes[] = [];
	private dialogRef: MatDialogRef<DialogManualSortingComponent, any>;
	public manualSorting: boolean;
	public type: number = TemplateTypes.ANALYSE;
	public galleries: GalleryItem[] = [];
	private ngUnsubscribe = new Subject();
	public enableReview: boolean;
	public alertaAtividades: AlertaAtividade[] = [];
	public alertaDocumentos: AlertaAtividade[] = [];
	public atividade: Atividade;
	public moduloAnaliseServiceCenter: boolean;
	public serviceCenterTemplate: Template;
	
	constructor (
		private fb: FormBuilder,
		private esteiraService: EsteiraService,
		private router: Router,
		public dialog: MatDialog,
		private generalInformationsService: GeneralInformationsService,
		private utilsService: UtilsService,
	) { }
	
	ngOnInit() {
		this.generalInformationsService.getPauseTypes().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
			response => {
				this.reasonsToPause = [...response];
			}
		);

		this.formPauses = this.fb.group({
			pause : [''],
			finishExpedient : [''],
			reasons: [''],
		})

		this.getNewItem();
	}

	ngOnDestroy() {
		clearInterval(this.interval);
		this.ngUnsubscribe.next();
		this.ngUnsubscribe.complete();
		localStorage.removeItem('displayImages');
	}

	private getNewItem(): void {
		
		if (this.interval === undefined) {
			this.loading = false;
		}
		
		this.galleries = [];
		this.template = null;
		this.manualSorting = false;
		this.moduloAnaliseServiceCenter = false;
		localStorage.removeItem('displayImages');
	
		this.esteiraService.getNextItem().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
			response => {
				if (response) {
					
					clearInterval(this.interval);
					this.esteiraID = response.id;
					if (response.template && response.template.header){
						this.agencia = response.template.header.bankBranch;
						this.documento = response.template.header.docNumber;
						this.contaCorrente = response.template.header.bankAccount;
					}
					
					this.galleries = response.galleries;
					this.enableReview = response.enableReview;
					this.alertaAtividades = response.alertaAtividades;
					this.alertaDocumentos = response.alertaDocumentos;
					this.atividade = response.atividade;
					
					if (response.atividade.id === 90010100) {
						this.manualSorting = true;
					} else if(response.atividade.moduloAnaliseServiceCenter == true) {
						this.moduloAnaliseServiceCenter = true;
						this.content = response.content;
						this.serviceCenterTemplate = response.template;
					} else {
						this.manualSorting = false;

						/*** Bind dos campos do template mediante carga */
						if (response.content) {
							this.content = (
								response.content.root == undefined
								? response.content
								: response.content.root
							);

							response.template.steps.map((step) => {
								step.fieldGroups.map((fieldGroup) => {
									fieldGroup.fields.map((field) => {
										Object.keys(this.content).forEach((key, index) => {
											if (field.dataSource) {
												if (key.toUpperCase() === field.dataSource.toUpperCase()) {
													if (field.mask !== "") {
														field.value = this.utilsService.adjustMask(field.mask, Object.values(this.content)[index]);
													} else {
														field.value = Object.values(this.content)[index];
													}
												}
											}
										})
									})
								})
							})	
						}
				
						this.template = response.template;
					}
					this.loading = false;
					
				} else {
					if (this.interval === undefined) {
						this.waitNewItem();
					}
				}
			},
			() => {
				setTimeout(() => {
					this.getNewItem();
				}, 3000)
			}
		);
	}

	private waitNewItem(): void {
		this.interval = setInterval(() => {
			this.getNewItem();
		}, 3000)
	}
	
	public submit(body: Analyse): void {
		body.receberProximo = false;
		body.templateVersaoId = this.template.templateVersaoId;
		body = this.setPauseConfigurations(body);

		this.loading = true;
		if (body.statusId) {
			this.esteiraService.postAnalyse(body, this.esteiraID).pipe(takeUntil(this.ngUnsubscribe)).subscribe(
				() => {
					this.loading = false;
					const dialog: Dialog = new Dialog();
					dialog.title = "Sucesso!";
					dialog.message = "Análise concluída com sucesso.";
					dialog.type = DialogTypes.INFORMATION;
					this.showMessage(dialog);
	
					this.dialogRef.afterClosed().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
						()=> {
							this.navigateHome(body);
						},
						() => {
							this.loading = false;
							this.errorMessage(null);
						}
					);
				},
				(error) => {
					this.loading = false;
					const dialog: Dialog = new Dialog();
					dialog.title = "Análise não concluída";
					dialog.message = error.error.message;
					dialog.type = DialogTypes.INFORMATION;
					this.showMessage(dialog);
				}
			)
		} else {
			this.navigateHome(body);
		}

	}

	public submitServiceCenter(body: Analyse): void {
		body = this.setPauseConfigurations(body);

		this.loading = true;
		if (body.statusId) {
			this.esteiraService.postAnalyse(body, this.esteiraID).pipe(takeUntil(this.ngUnsubscribe)).subscribe(
				() => {
					this.loading = false;
					const dialog: Dialog = new Dialog();
					dialog.title = "Sucesso!";
					dialog.message = "Análise concluída com sucesso.";
					dialog.type = DialogTypes.INFORMATION;
					this.showMessage(dialog);
	
					this.dialogRef.afterClosed().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
						()=> {
							this.navigateHome(body);
						},
						() => {
							this.loading = false;
							this.errorMessage(null);
						}
					);
				},
				(error) => {
					this.loading = false;
					const dialog: Dialog = new Dialog();
					dialog.title = "Análise não concluída";
					dialog.message = error.error.message;
					dialog.type = DialogTypes.INFORMATION;
					this.showMessage(dialog);
				}
			)
		} else {
			this.navigateHome(body);
		}

	}

	private navigateHome(body): void{
		if (body.entrarPausa || body.finalizarExpediente) {
			this.router.navigate(['/inicial/home']);
		} else {
			this.getNewItem();
		}
	}

	public submitManualSorting(body: PostManualSorting): void {
		if (this.validationsToSubmit()) {
			
			body = this.setPauseConfigurations(body);
			this.loading = true;
			
			this.esteiraService.postManualSorting(body, this.esteiraID).pipe(takeUntil(this.ngUnsubscribe)).subscribe(
				() => {
					const dialog: Dialog = new Dialog();
					this.loading = false;
					dialog.title = "Sucesso!";
					dialog.message = "Análise concluída com sucesso.";
					dialog.type = DialogTypes.INFORMATION;
					this.showMessage(dialog);

					this.dialogRef.afterClosed().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
						() => {
							this.navigateHome(body);
						},
						() => {
							this.loading = false;
							this.errorMessage(null);
						}
					);
				}, 
				error => {
					this.loading = false;
					if (error.status === 404 && error.error.code === 'SemCarga') {
						
						const dialog: Dialog = new Dialog();
						dialog.title = "Atenção";
						dialog.message = `${error.error.message}. <br />O que deseja fazer?`;
						dialog.type = DialogTypes.CONFIRMATION;
						this.showMessage(dialog);

						this.dialogRef.afterClosed().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
							response => {
								if (response !== 'cancel') {
									this.loading = true;
									body.doMatch = false;
									
									if (response === 'contingency') {
										body.isContingencia = true;
									} 
	
									this.esteiraService.postManualSorting(body, this.esteiraID).pipe(takeUntil(this.ngUnsubscribe)).subscribe(
										() => {
											this.loading = false;
											if (body.entrarPausa || body.finalizarExpediente) {
												this.router.navigate(['/inicial/home']);
											} else {
												this.getNewItem();
											}
										},
										() => {
											this.loading = false;
											this.errorMessage(null);
										}
									);
								}
							}
						);
					} else {
						this.loading = false;
						this.errorMessage(error.error.message);
					}
				}
			);
		}
	}

	private setPauseConfigurations(body: Analyse | PostManualSorting): any {
		if (this.formPauses.value.pause) {
			body.entrarPausa = true;
			body.finalizarExpediente = false;
			body.tipoPausaId = this.formPauses.value.reasons;
		} else if (this.formPauses.value.finishExpedient) {
			body.entrarPausa = false;
			body.finalizarExpediente = true;
			delete body.tipoPausaId;
		} else {
			body.entrarPausa = false;
			body.finalizarExpediente = false;
		}
		return body;
	}

	private validationsToSubmit(): boolean {
		if (this.formPauses.value.pause && this.formPauses.value.reasons === "") {
			const dialog: Dialog = new Dialog();
			dialog.title = "Motivo da Pausa";
			dialog.message = "Selecione o motivo pelo qual você vai entrar em pausa.";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
			return false;
		}

		return true;
	}
	
	private errorMessage(message: string = null): void {
		if (message == null) message = 'Não foi possível concluir a análise.';
		const dialog: Dialog = new Dialog();
		dialog.title = "Erro";
		dialog.message = message;
		dialog.type = DialogTypes.INFORMATION;
		this.showMessage(dialog);
	}
	
	public showMessage(dialog: Dialog): void {
		this.dialogRef = this.dialog.open(DialogManualSortingComponent, {
			width: '600px',
			disableClose: true,
			data: {
				title : dialog.title,
				message : dialog.message,
				type : dialog.type,
			}
		});
	}
	
	public setPause(event: MatCheckboxChange) {
		if (event.checked) {
			this.formPauses.get('finishExpedient').disable();
		} else {
			this.formPauses.get('finishExpedient').enable();
		}
	}

	public setFinishExpedient(event: MatCheckboxChange) {
		if (!event.checked) {
			this.formPauses.get('pause').enable();
		} else {
			this.formPauses.get('pause').disable();
		}
	}
}
