import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Dialog } from 'src/app/models/dialog.model';

@Component({
	selector: 'app-dialog-manual-sorting',
	templateUrl: './dialog-manual-sorting.component.html',
	styleUrls: ['./dialog-manual-sorting.component.scss']
})
export class DialogManualSortingComponent implements OnInit {

	constructor(
		public dialogRef: MatDialogRef<DialogManualSortingComponent>,
		@Inject(MAT_DIALOG_DATA) public data: Dialog,
	) { }
	
	ngOnInit() {

	}

	public decision(decision: string) {
		this.dialogRef.close(decision);
	}
}
