import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
	providedIn: 'root'
})
export class ReportProductivityService {
	public urlReportBuilder = "/api/v1/relatorios-produtividades";

	constructor (
		private http: HttpClient,
    ) { }
	
	public generate(exportaXLSX: boolean,
					maloteEnglobador: string, 
					dataInicial: string, 
					dataFinal: string, 
					PageSize: string, 
					PageIndex: string) {

		const data = {
				exportaXLSX: exportaXLSX,
				maloteEnglobador: maloteEnglobador,
				dataInicial : dataInicial,
				dataFinal: dataFinal,
				pageSize: PageSize,
				pageIndex: PageIndex
		};
		
		var options = {}
		if (exportaXLSX){
			options = {responseType : 'blob' as 'json'}
		}

		return this.http.post<any>(environment.urlServices + this.urlReportBuilder, data, options);
	}

}