import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';
import { ManagerialView } from 'src/app/models/managerial-view.interface';
import { ResponseList } from 'src/app/models/response-list.interface';


@Injectable({ providedIn: 'root' })
export class ManagerialViewService {

    constructor (
        private http: HttpClient,
    ) {}


    getDataManagerialView(): Observable<ManagerialView> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/visoes-gerenciais`).pipe(
            map(response => response.data)
        );
    }
    
    getDetailManagerialViewOperation(operacaoId: string, kpi: string, pageSize: number, pageIndex): Observable<ResponseList> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/visoes-gerenciais/operacao/${operacaoId}`, {
            params : {
                Kpi: kpi,
                PageSize: pageSize.toString(),
                PageIndex: pageIndex.toString()
            }
        })
    }
    
    getDetailManagerialViewActivity(operacaoId: string, atividadeId: string, kpi: string, pageSize: number, pageIndex): Observable<ResponseList> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/visoes-gerenciais/operacao/${operacaoId}/atividade/${atividadeId}`, {
            params : {
                Kpi: kpi,
                PageSize: pageSize.toString(),
                PageIndex: pageIndex.toString()
            }
        })
    }
    
}