import { Component, OnInit, ViewChild, ElementRef, AfterViewInit, ViewChildren, QueryList, OnDestroy, HostListener   } from '@angular/core';
import { GalleryItem } from 'src/app/models/gallery-item.interface';
import { AutenticationService } from 'src/app/services/autentication.service';
import { User } from 'src/app/models/user.model';
import { MatDialogRef, MatDialog } from '@angular/material';
import { DialogImageEditComponent } from './dialog-image-edit/dialog-image-edit.component';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { Dialog } from 'src/app/models/dialog.model';

@Component({
	selector: 'app-display-images',
	templateUrl: './display-images.component.html',
	styleUrls: ['./display-images.component.scss']
})
export class DisplayImagesComponent implements OnInit, AfterViewInit, OnDestroy {
	public loading: boolean = true;
	public mediaGallery: GalleryItem[] = [];
	public document: string;
	@ViewChild('wrappImages', {static: true}) wrappImages: ElementRef;
	@ViewChildren('images') images:  QueryList<ElementRef>;
	public heightWrapper: number = 0;
	public indexActive: number = 0;
	public user: User;
	public activeImage: string;

	constructor(private autenticationService: AutenticationService, public dialog: MatDialog) {
		autenticationService.$loggedSubject.next(false);
	}

	public mouseMove(idImage: string, item: number) {
		this.indexActive = item;
		if(this.activeImage != idImage) {
			const thumb = document.getElementById("thumb_" + idImage);
			thumb.scrollIntoView();
			this.activeImage = idImage;
		}
	}
	
	ngOnInit() {
		this.user = this.autenticationService.getDataUser();
		const displayImages = JSON.parse(localStorage.getItem('displayImages'));
		this.document = displayImages.document;
		this.mediaGallery = displayImages.images;
		this.document = displayImages.document;
		this.heightWrapper = window.innerHeight - 54;
	}

	ngAfterViewInit() {
		setTimeout(() => {
			this.loading = false;
		}, 1000)

		setInterval(() => {
			if (!localStorage.getItem('displayImages')) {
				window.close();
			}
		}, 1000)
	}

	ngOnDestroy() {
		localStorage.removeItem('displayImages');
	}

	public compare() {
		this.openModalEdit(this.indexActive,true);
	}

	public setPosition(item: number) {
		this.indexActive = item;
		this.wrappImages.nativeElement.scrollTop = this.images.find((el, index) => index === item).nativeElement.offsetTop - 20;
	}

	onResize() {
		this.heightWrapper = window.innerHeight - 54;
	}

	public openModalEdit(indiceSelected: number, compare: boolean){
		const dialog: Dialog = new Dialog();
		dialog.title = "Visualizar Imagem";
		dialog.message = "Editar Imagem.";
		dialog.type = DialogTypes.CONFIRMATION;
		const dialogRef = this.dialog.open(DialogImageEditComponent, {
			height: '100%',
			width: '100%',
			maxWidth: '100%',
			panelClass: 'image-edit-panel',
			data: {
				images: this.mediaGallery,
				indiceSelected: indiceSelected,
				compare: compare
			}
		});

		dialogRef.afterClosed().subscribe( 
			result => {
				if(result !== undefined ) {
				}
				
			},
			() => {
				
			}
		);
	}
}
